.contact{
    a{
        display: flex;
        align-items: flex-end;
        &:not(:last-of-type){
            margin-bottom: 8px;
        }
        i{
            margin-right: 10px;
            font-size: 15px;
            &.icon-1-8_email{
                font-size: 12px;
            }
        }
    }
}
.list-line{
    display: flex;
    li{
        border-left: 1px solid var(--Whisper);
        padding: 33px 21px 27px;
        display: flex;
        align-items: center;
        &:last-of-type{
            padding-right: 13px;
        }
    }
}
.popup-desc{
    position: absolute;
    background-color: var(--White);
    padding: 6px 10px 5px;
    font-size: 13px;
    color: var(--mainColor);
    border-radius: 5px;
    z-index: 2;
    opacity: 0;
    top: -33px;
    left: -16px;
    transform: translateY(50%);
    transition: transform .3s,opacity .2s, display 0.2s;
    pointer-events: none;
    &::after{
        position: absolute;
        content: "";
        border: 6px solid transparent;
        border-top: 6px solid var(--White);
        top: 100%;
        left: 20px;
    }
    &-parent{
        position: relative;
        &:hover{
            overflow: inherit;
            .popup-desc{
                opacity: 1;
                transform: translateY(0);
                transition: transform .3s,opacity .4s;
            }
        }
    }
}
.elect{
    color: var(--DarkGray);
    i{
        color: var(--mainColor);
        font-size: 19px;
    }
    &.not-empty{
        color: var(--mainColor);
        i{
            color: var(--Red);
        }
    }
}
.comparison{
    color: var(--DarkGray);
    i{
        color: var(--mainColor);
        font-size: 26px;
    }
    &.not-empty{
        color: var(--mainColor);
        i{
            color: var(--Wedgewood);
        }
    }
}
.basket{
    i{
        font-size: 23px;
    }
    &.not-empty{
        i{
            color: var(--Flamenco);
        }
    }
    &__inner{
        span{
            display: block;
        }
    }
}
.menu-mobile__inner .basket__inner {
    span:not(:last-of-type){
        margin-bottom: 10px;
    }
}
.logIn{
    i{
        font-size: 20px;
    }
    &.not-empty{
        i{
            color: var(--Wedgewood);
        }
    }
}
.elect, .comparison, .basket, .logIn{
    display: flex;
    align-items: center;
    padding: 0 0.5px;
    i{
        margin-right: 10px;
    }
}
.brends-list{
    a{
        text-decoration: none;
        color: var(--mainColor);
        transition: all .3s ease-out;
        &:hover{
            color: var(--Flamenco);
            transition: all .3s ease-out;
        }
        &.defLink{
            color: var(--Charcoal);
            border-bottom: 1px dashed currentColor;
            transition: all .3s ease-out;
            display: inline-block;
            &:hover{
                color: var(--Wedgewood);
                transition: all .3s ease-out;
            }
        }
    }
}
.icon-wrap{
    max-width: 50px;
    width: 100%;
    margin-right: 20px;
    display: flex;
    width: 100%;
    justify-content: center;
    i{
        margin-right: 0;
    }
}
.networks{
    i{
        font-size: 25px;
        &.icon-2-7_wk{
            font-size: 19px;
        }
    }
    a:not(:last-of-type){
        margin-right: 25px;
    }
}
.border-style{
    border: 1px solid var(--Gainsboro);
    padding: 22px 0px 17px;
    margin-bottom: 40px;
}
.service-list{
    li{
        display: flex;
        align-items: center;
        padding: 11px 35px 11px 20px;
        a {
            flex-shrink: 1;
        }
        &:hover{
            background-color: var(--Wedgewood);
            transition: all .3s ease-out;
            i, a{
                color: var(--White);
                transition: all .3s ease-out;
            }
            .dropdown a {
                color: var(--mainColor);
            }
        }
        i{
            font-size: 42px;
            color: var(--Wedgewood);
            &.icon-4-9_service2{
                font-size: 9px;
            }
        }
    }
    .arrowDown{
        &::after{
            color: var(--VeryLightGrey);
            font-size: 7px;
            transform: rotate(-90deg);
            right: 16px;
        }
        &:hover{
            &::after{
                transform: rotate(-90deg);
                color: var(--Flamenco);
            }
        }
    }
    .dropdown {
        width: 353px;
        left: 100%;
        top: -23px;
        z-index: 3;
        li:hover {
            background-color: var(--White);
        }
        .h-Flamenco-1:hover {
            color: var(--Flamenco);
        }
    }
}
.banner-advertising{
    img{
        width: 100%;
    }
}
.Full-block__outer {
    background-color: #152234;
}
.block__wrap1{
    max-width: 940px;
    flex: 0 0 100%;
}
.block__wrap2{
    max-width: calc(100% - 940px);
    flex: 0 0 100%;
    display: flex;
    align-items: flex-start;
}
.file-wrap{
    padding-top: 50px;
    display: inline-block;
    position: relative;
    margin-top: 15px;
    .Btn{
        position: absolute;
        line-height: normal;
        top: 0;
        left: 0;
    }
}
.attachedFile{
    display: block;
    p{
        margin: 0;
        display: flex;
        align-items: center;
        i{
            font-size: 12px;
            margin-left: 10px;
            cursor: pointer;
            &:hover{
                color: var(--Red);
            }
        }
    }
}
.card-action{
    display: flex;
    justify-content: center;
    .icon-2-4_elect{
        font-size: 21px;
    }
    .icon-1-5_comparison{
        font-size: 26px;
    }
    button{
        width: 50px;
        height: 40px;
        padding: 0 0 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        background-color: transparent;
        border: none;
        cursor: pointer;
        transition: all .3s ease-out;
        &:focus{
            outline: none;
        }
        &::after{
            position: absolute;
            content: "";
            width: 100%;
            height: 3px;
            background-color: var(--Gainsboro);
            bottom: 0;
            left: 0;
            transition: all .3s ease-out;
        }
        &:not(:last-of-type){
            margin-right: 10px;
        }
        &:hover{

            &::after{
                transition: all .3s ease-out;
                background-color: #e08b44;
            }

        }
    }
    .electBtn.active, .electBtn:hover{
       color: var(--Red);
       transition: all .3s ease-out;
    }
    .comparisonBtn.active, .comparisonBtn:hover{
       color: var(--Wedgewood);
       transition: all .3s ease-out;
    }
    &__bottom{
        display: flex;
        justify-content: center;
    }
}
.price{
    font-size: 14px;
    display: flex;
    justify-content: center;
    span{
        font-weight: 700;
        color: var(--mainColor);
        white-space: nowrap;
    }
    del{
        font-weight: 400;
        color: var(--Charcoal);
        margin-left: 18px;
        white-space: nowrap;
    }
}
.count{
    display: flex;
    width: 110px;
    border-radius: 10px;
    border: 1px solid var(--Gainsboro);
    padding: 7px 10px;
    margin: 0 auto;
    button{
        border: none;
        background-color: var(--Gainsboro);
        border-radius: 50%;
        font-size: 7px;
        padding: 0;
        width: 21px;
        height: 21px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: all .3s ease-out;
        &:focus{
            outline: none;
        }
        &:hover, &:active{
            background-color: #445f87;
            transition: all .3s ease-out;
            color: #ffffff;
        }
    }
    input{
        width: 46px;
        padding: 0 8px;
        text-align: center;
        border: none;
        &:focus{
            outline: none;
        }
    }
    &__wrap{
        span{
            margin: 3px 0 0;
            font-size: 13px;
            color: var(--Charcoal);
            line-height: normal;
            display: block;
            text-align: center;
        }
    }
}
.badge{
    position: absolute;
    font-size: 13px;
    color: var(--White);
    padding: 9px 9px 10px;
    border-radius: 10px;
    background-color: var(--Wedgewood);
    left: 10px;
    top: 10px;
    line-height: 10px;
    z-index: 1;
    &-parent{
        position: relative;
    }
    &.first{
        top: 10px;
    }
    &.second{
        top: 42px;
    }
    &.third{
        top: 75px;
    }
    &.red{
        background-color: var(--Red);
    }
    &.orange{
        background-color: var(--Flamenco);
    }
    &.text{
        color: var(--PuertoRico);
        left: auto;
        right: 10px;
        background-color: transparent;
        font-weight: 600;
    }
}
.Instock{
    font-size: 13px;
    font-weight: 600;
    color: var(--PuertoRico);
}
.search-page{
    margin-bottom: 40px;
    .section-title{
        margin-bottom: 30px;
    }
}
.search_block{
    display: flex;
    padding-bottom: 20px;
    .input{
        border: 1px solid var(--Gainsboro);
        transition: all .3s ease-out;
        margin-right: 10px;
        max-width: 566px;
    }
    .Btn-Wedgewood{
        width: 147px;
    }
}
@media(max-width: 1400px){
    .block__wrap1{
        max-width: 740px;
    }
    .block__wrap2{
        max-width: calc(100% - 740px);
    }
}
@media(max-width: 1199px){
    .block__wrap1{
        max-width: 540px;
    }
    .block__wrap2{
        max-width: calc(100% - 540px);
    }
}
@media(max-width: 1024px){
    .block__wrap1{
        max-width: 100%;
    }
    .block__wrap2{
        max-width: 100%;
    }
}
@media(max-width: 767px){
    .Full-block{
        &::before{
            left: -10px;
            width: calc(100% + 20px);
        }
    }
    .main-inner {
        overflow: visible;
    }
    .subcategory-page .pagination {
        margin-bottom: 10px;
    }
    .search_block {
        .input{
            max-width: 100%;
        }
    }
    .search-page{
        margin-bottom: 10px;
    }
}
@media(max-width: 575px){
    .search_block {
        .Btn-Wedgewood{
            width: 75px;
            padding: 7px 16px;
        }
    }
}